* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "Qanelas Light";
  src: url("../fonts/Qanelas-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Qanelas Regular";
  src: url("../fonts/Qanelas-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Qanelas Medium";
  src: url("../fonts/Qanelas-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Qanelas SemiBold";
  src: url("../fonts/Qanelas-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Qanelas Bold";
  src: url("../fonts/Qanelas-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
html {
  scroll-behavior: smooth;
}

* {
  line-height: 130%;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  background: #ffffff;
}
.header__header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  max-width: 1130px;
  width: 100%;
  margin: 0 auto;
  padding: 15px 10px 10px 10px;
  border-bottom: 0.3px solid #E2E2E2;
  background: #ffffff;
}
.header__logo {
  max-width: 255px;
  height: 96px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding-right: 15px;
  border-right: 0.3px solid #E2E2E2;
  font-family: "Qanelas Bold";
  font-size: 36px;
  color: #093D88;
}
.header__wrapper {
  max-width: 840px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  z-index: 6;
  transition: all ease 0.8s;
}
.header__phone {
  max-width: 240px;
  width: 100%;
  padding-right: 15px;
  border-right: 0.3px solid #E2E2E2;
  height: 96px;
  padding-top: 10px;
}
.header__telegram {
  margin-bottom: 7px;
}
.header__viber, .header__telegram {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: unset;
  align-items: center;
  flex-direction: row;
}
.header__viber img, .header__telegram img {
  margin-right: 15px;
}
.header__viber p, .header__telegram p {
  font-family: "Qanelas Medium";
  font-size: 22px;
}
.header__viber b, .header__telegram b {
  font-family: "Qanelas Light";
  font-size: 16px;
}
.header__schedule {
  max-width: 290px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding-right: 15px;
  border-right: 0.3px solid #E2E2E2;
  height: 96px;
}
.header__schedule p {
  font-family: "Qanelas Regular";
  font-size: 15px;
}
.header__consultation {
  position: relative;
  padding: 12px 11px 12px 28px;
  font-family: "Qanelas SemiBold";
  font-size: 14px;
  color: #ffffff;
  text-transform: uppercase;
  background: linear-gradient(93.92deg, #4B9ED7 13.08%, #093D88 129.55%);
  border-radius: 5px;
  cursor: pointer;
}
.header__consultation img {
  position: absolute;
  left: -8px;
  top: -15px;
}
.header .burger-btn {
  display: none;
  z-index: 10;
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
}
.header .burger-btn > span {
  position: absolute;
  background-color: #093D88;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 9px;
  transition: all ease 0.5s;
}
.header .burger-btn:after, .header .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #093D88;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header .burger-btn:before {
  top: 20px;
}
.header .burger-btn:after {
  bottom: 20px;
}
.header .burger-btn_active span {
  opacity: 0;
}
.header .burger-btn_active:before {
  transform: rotate(45deg);
  top: 9px;
}
.header .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header__menu {
  padding: 20px 0;
  background: linear-gradient(93.92deg, #4B9ED7 13.08%, #093D88 129.55%);
}
.header__links {
  max-width: 620px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  color: #ffffff;
}
.header__links a {
  position: relative;
  max-width: 145px;
  font-family: "Qanelas Regular";
  font-size: 20px;
  color: #ffffff;
  padding-bottom: 2px;
  transition: all ease 0.5s;
}
.header__links a:after {
  position: absolute;
  content: "";
  width: 0;
  height: 2px;
  background: #ffffff;
  bottom: 0;
  right: 0;
  left: 0;
  transition: all 0.5s;
}
.header__links a:hover:after {
  width: 100%;
}
@media screen and (max-width: 1110px) {
  .header__header {
    padding-left: 20px;
    padding-right: 20px;
  }
  .header__logo {
    height: unset;
    border-right: 0;
  }
  .header .burger-btn {
    display: block;
  }
  .header__wrapper {
    position: absolute;
    max-width: none;
    text-align: center;
    z-index: 2;
    top: -225%;
    left: 0;
    width: 100%;
    padding: 70px 20px 20px 20px;
    background: #E2E2E2;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .header__wrapper_active {
    top: 0;
    box-shadow: 0 0 25px 40px rgba(0, 0, 0, 0.25);
  }
  .header__schedule {
    margin-left: 50px;
    text-align-last: left;
  }
}
@media screen and (max-width: 500px) {
  .header__header {
    padding-left: 10px;
    padding-right: 10px;
  }
  .header__logo {
    max-width: 185px;
  }
  .header__logo img {
    width: 50px;
  }
  .header__logo p {
    font-size: 24px;
  }
  .header__wrapper {
    top: -230%;
  }
  .header__wrapper_active {
    top: 0;
  }
}
@media screen and (max-width: 420px) {
  .header__links a {
    font-size: 14px;
  }
  .header__wrapper {
    top: -245%;
  }
  .header__wrapper_active {
    top: 0;
  }
}
@media screen and (max-width: 360px) {
  .header__wrapper {
    align-items: baseline;
    padding-right: 10px;
  }
  .header__schedule {
    margin-left: 0;
    padding-right: 0;
  }
  .header__schedule p {
    font-size: 14px;
  }
}

.popup-consultation {
  position: fixed;
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  z-index: 10;
  background: rgba(0, 0, 0, 0.4);
  transition: all ease 0.5s;
}
.popup-consultation__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 320px;
  width: 100%;
}
.popup-consultation__content {
  position: relative;
  padding: 20px;
  width: 100%;
  background: #E2E2E2;
  border-radius: 5px;
}
.popup-consultation__close {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 28px;
  height: 28px;
  background: #093D88;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
}
.popup-consultation__logo {
  max-width: 190px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin: 0 auto;
  font-family: "Qanelas Bold";
  font-size: 36px;
  color: #093D88;
  margin-bottom: 15px;
}
.popup-consultation h3 {
  font-family: "Qanelas SemiBold";
  font-size: 24px;
  color: #093D88;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 5px;
}
.popup-consultation h4 {
  font-family: "Qanelas SemiBold";
  font-size: 16px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 20px;
}
.popup-consultation form {
  width: 100%;
}
.popup-consultation input[type=text],
.popup-consultation input[type=tel] {
  width: 100%;
  background-color: #ffffff;
  border-radius: 5px;
  border: 0;
  padding: 15px 15px 15px 60px;
  font-family: "Qanelas Regular";
  font-size: 14px;
  color: #1c1e27;
}
.popup-consultation input[type=text]::-webkit-input-placeholder,
.popup-consultation input[type=tel]::-webkit-input-placeholder {
  color: #1c1e27;
}
.popup-consultation__name {
  margin-bottom: 10px;
  background: url(../img/header/name.png) no-repeat 16px 11px;
}
.popup-consultation__tel {
  margin-bottom: 20px;
  background: url(../img/header/number.png) no-repeat 21px 8px;
}
.popup-consultation__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.popup-consultation form {
  display: flex;
  flex-direction: column;
}
.popup-consultation form label {
  margin-bottom: 10px;
}
.popup-consultation form label,
.popup-consultation form p {
  font-family: "Qanelas Regular";
  font-size: 12px;
  color: #000000;
}
.popup-consultation form p {
  margin-bottom: 25px;
}
.popup-consultation form p b {
  font-family: "Qanelas Regular";
  font-size: 16px;
}
.popup-consultation__checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.popup-consultation__checkbox + label::before {
  content: "";
  display: inline-block;
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 0;
  background-color: #ffffff;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  cursor: pointer;
}
.popup-consultation__checkbox:checked + label::before {
  border-color: #093D88;
  background-color: #093D88;
  background-image: url("../img/header/check.png");
  background-size: 12px;
}
.popup-consultation button {
  padding: 15px 55px 15px 45px;
  font-family: "Qanelas Medium";
  font-size: 16px;
  color: #ffffff;
  background-color: #093D88;
  text-transform: uppercase;
  margin: 0 auto;
  border-radius: 5px;
  background-image: url("../img/header/arrow.png");
  background-repeat: no-repeat;
  background-position-x: 155px;
  background-position-y: 15px;
}

