.card {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 650px) {
  .card {
    flex-direction: column;
  }
}
.card__item {
  padding-bottom: 50px;
  margin-bottom: 30px;
  padding-right: 100px;
  margin-right: 25px;
  width: max-content;
}
@media screen and (min-width: 1130px) {
  .card__item:nth-child(4n) .card__text-wrapper, .card__item:nth-child(4n+1) .card__text-wrapper {
    background: #093D88;
  }
  .card__item:nth-child(4n) .card__text-wrapper, .card__item:nth-child(4n+1) .card__text-wrapper {
    color: #ffffff;
  }
  .card__item:nth-child(4n) .card__title, .card__item:nth-child(4n+1) .card__title {
    color: #000000;
  }
}
@media screen and (max-width: 1129px) {
  .card__item:nth-child(2n) .card__text-wrapper {
    background: #E2E2E2;
  }
  .card__item:nth-child(2n) .card__text-wrapper {
    color: #000000;
  }
  .card__item:nth-child(2n) .card__title {
    color: #093D88;
