}
.card__inner-wrapper {
  position: relative;
}
.card__inner-wrapper::before {
  content: "";
  position: absolute;
  width: 50%;
  height: 40%;
  top: 0;
  left: 20%;
  border: 2px solid #4b9ed7;
  border-radius: 20px;
  transform: rotateZ(-15deg);
}
@media screen and (max-width: 650px) {
  .card__inner-wrapper::before {
    top: 50px;
    left: 120px;
