}
.card__inner-wrapper::after {
  content: "";
  z-index: -2;
  position: absolute;
  width: 50%;
  height: 40%;
  bottom: -3%;
  left: 15%;
  border: 2px solid black;
  border-radius: 20px;
  transform: rotateZ(-15deg);
}
@media screen and (max-width: 650px) {
  .card__inner-wrapper::after {
    height: 30%;
