  transition: all ease 0.5s;
}
.question__header h4 {
  font-family: "Qanelas Medium";
  font-size: 16px;
  color: #000000;
}
.question__header_active {
  background: #4b9ed7;
}
.question__header_active h4 {
  color: #ffffff;
}
.question__plus {
  position: relative;
  width: 28px;
  height: 28px;
  background: #093D88;
  border-radius: 45px;
  cursor: pointer;
  transition: all ease 0.5s;
}
.question__plus > span {
  position: absolute;
  top: 13px;
  left: 6px;
  width: 16px;
  height: 2px;
  background: #ffffff;
  transition: all ease 0.5s;
}
.question__plus::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 13px;
  width: 2px;
  height: 16px;
  background: #ffffff;
  transition: all ease 0.5s;
}
.question__plus_active {
  background: #ffffff;
}
.question__plus_active span, .question__plus_active::after {
  background: #093D88;
}
.question__plus_active span {
  opacity: 0;
}
.question__plus_active::after {
  transform: rotate(90deg);
}
.question__text {
  position: relative;
  z-index: -1;
  transform: translateY(-300px);
  width: 100%;
  padding: 0 30px;
  font-family: "Qanelas Regular";
  font-size: 16px;
  color: #000000;
  z-index: -1;
  transition: all ease 0.8s;
}
.question__text_active {
  transform: translateY(0);
}
.question__form {
