.question__form input[type=text]::-webkit-input-placeholder,
.question__form input[type=tel]::-webkit-input-placeholder,
.question__form textarea::-webkit-input-placeholder {
  color: #1c1e27;
}
.question__form textarea {
  flex-basis: 60%;
