.question__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.question form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.question form label,
.question form p {
  font-family: "Qanelas Regular";
  font-size: 12px;
  color: #000000;
