  font-family: "Qanelas Regular";
  font-size: 16px;
}
.question__checkbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.question__checkbox + label::before {
  content: "";
  display: inline-block;
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid;
  background-color: #ffffff;
