.contact {
  width: 100%;
  position: relative;
}
.contact__content {
  position: relative;
  max-width: 350px;
  width: 100%;
  padding: 55px 40px;
  background: #093D88;
}
.contact__content::after {
  content: "";
  position: absolute;
  z-index: -1;
  border: 60px solid transparent;
  border-top: 196px solid #093d88;
  bottom: -60px;
  right: -60px;
}
.contact__content::before {
  content: "";
  position: absolute;
  top: -60px;
  right: -60px;
  border: 60px solid transparent;
  border-bottom: 90px solid #093d88;
}
.contact__header {
  position: relative;
  max-width: 200px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
  font-family: "Qanelas Regular";
  font-size: 18px;
  color: #ffffff;
}
.contact__telegram {
  position: absolute;
  top: 10px;
  right: -50px;
}
.contact__viber {
  position: absolute;
  top: 10px;
  right: -100px;
}
.contact__text {
  font-family: "Qanelas Regular";
  font-size: 16px;
  color: #ffffff;
}
.contact__map {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -2;
}
@media screen and (max-width: 768px) {
  .contact {
    overflow: hidden;
  }
  .contact__content {
    max-width: 225px;
    padding: 20px;
  }
  .contact__content::after {
    border-top: 132px solid #093d88;
  }
  .contact__content::before {
    border-bottom: 55px solid #093d88;
  }
  .contact__header {
    max-width: 150px;
    font-size: 14px;
  }
  .contact__logo {
    width: 40px;
  }
  .contact__telegram {
    right: -40px;
    width: 25px;
  }
  .contact__viber {
    right: -75px;
    width: 25px;
  }
  .contact__text {
    max-width: 200px;
    font-size: 14px;
  }
  .contact__map {
    height: 202px;
  }
}
@media screen and (max-width: 500px) {
  .contact__content {
    max-width: 225px;
    padding: 20px;
  }
  .contact__content::after {
    border: 40px solid transparent;
    border-top: 132px solid #093d88;
    bottom: -40px;
    right: -40px;
  }
  .contact__content::before {
    top: -40px;
    right: -40px;
    border: 40px solid transparent;
    border-bottom: 55px solid #093d88;
  }
}

