@import "./nullstyle";
@import "./variable";
@import "./fonts";
@import "./mixins";

html {
  scroll-behavior: smooth;
}

* {
  line-height: 130%;
}

//                                  HEADER

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  background: $contrasr-color;
  &__header {
    @include flex(space-between);
    max-width: 1130px;
    width: 100%;
    margin: 0 auto;
    padding: 15px 10px 10px 10px;
    border-bottom: 0.3px solid $secondary-color;
    background: $contrasr-color;
  }
  &__logo {
    max-width: 255px;
    height: 96px;
    width: 100%;
    @include flex(space-between);
    padding-right: 15px;
    border-right: 0.3px solid $secondary-color;
    font-family: $font-family-bold;
    font-size: 36px;
    color: $primary-color;
  }
  &__wrapper {
    max-width: 840px;
    width: 100%;
    @include flex(space-between);
    z-index: 6;
    @include transition(0.8s);
  }
  &__phone {
    max-width: 240px;
    width: 100%;
    padding-right: 15px;
    border-right: 0.3px solid $secondary-color;
    height: 96px;
    padding-top: 10px;
  }
  &__telegram {
    margin-bottom: 7px;
  }
  &__viber,
  &__telegram {
    width: 100%;
    @include flex($justify-content: unset);
    img {
      margin-right: 15px;
    }
    p {
      font-family: $font-family-medium;
      font-size: 22px;
    }
    b {
      font-family: $font-family-light;
      font-size: 16px;
    }
  }
  &__schedule {
    max-width: 290px;
    width: 100%;
    @include flex(space-between);
    padding-right: 15px;
    border-right: 0.3px solid $secondary-color;
    height: 96px;
    p {
      font-family: $font-family-regular;
      font-size: 15px;
    }
  }
  &__consultation {
    position: relative;
    padding: 12px 11px 12px 28px;
    font-family: $font-family-semibold;
    font-size: 14px;
    color: $contrasr-color;
    text-transform: uppercase;
    background: $gradient;
    border-radius: 5px;
    cursor: pointer;
    img {
      position: absolute;
      left: -8px;
      top: -15px;
    }
  }
  .burger-btn {
    display: none;
    z-index: 10;
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;
    > span {
      position: absolute;
      background-color: $primary-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      top: 9px;
      @include transition;
    }
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: $primary-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      @include transition;
    }
    &:before {
      top: 20px;
    }
    &:after {
      bottom: 20px;
    }
    &_active {
      span {
        opacity: 0;
      }
      &:before {
        transform: rotate(45deg);
        top: 9px;
      }
      &:after {
        transform: rotate(-45deg);
        bottom: 9px;
      }
    }
  }
  &__menu {
    padding: 20px 0;
    background: $gradient;
  }
  &__links {
    max-width: 620px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
    @include flex(space-between);
    color: $contrasr-color;
    a {
      position: relative;
      max-width: 145px;
      font-family: $font-family-regular;
      font-size: 20px;
      color: $contrasr-color;
      padding-bottom: 2px;
      @include transition;
      &:after {
        position: absolute;
        content: "";
        width: 0;
        height: 2px;
        background: $contrasr-color;
        bottom: 0;
        right: 0;
        left: 0;
        transition: all 0.5s;
      }
      &:hover {
        &:after {
          width: 100%;
        }
      }
    }
  }
  @media screen and (max-width: 1110px) {
    &__header {
      padding-left: 20px;
      padding-right: 20px;
    }
    &__logo {
      height: unset;
      border-right: 0;
    }
    .burger-btn {
      display: block;
    }
    &__wrapper {
      position: absolute;
      max-width: none;
      text-align: center;
      z-index: 2;
      top: -225%;
      left: 0;
      width: 100%;
      padding: 70px 20px 20px 20px;
      background: $secondary-color;
      @include flex($flex-direction: column);
      &_active {
        top: 0;
        box-shadow: 0 0 25px 40px rgb(0 0 0 / 25%);
      }
    }
    &__schedule {
      margin-left: 50px;
      text-align-last: left;
    }
  }
  @media screen and (max-width: 500px) {
    &__header {
      padding-left: 10px;
      padding-right: 10px;
    }
    &__logo {
      max-width: 185px;
      img {
        width: 50px;
      }
      p {
        font-size: 24px;
      }
    }
    &__wrapper {
      top: -230%;
      &_active {
        top: 0;
      }
    }
  }
  @media screen and (max-width: 420px) {
    &__links {
      a {
        font-size: 14px;
      }
    }
    &__wrapper {
      top: -245%;
      &_active {
        top: 0;
      }
    }
  }
  @media screen and (max-width: 360px) {
    &__wrapper {
      align-items: baseline;
      padding-right: 10px;
    }
    &__schedule {
      margin-left: 0;
      padding-right: 0;
      p {
        font-size: 14px;
      }
    }
  }
}

//                                  POPUP CONSULTATION

.popup-consultation {
  position: fixed;
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  z-index: 10;
  background: #0006;
  @include transition;
  &__body {
    min-height: 100%;
    display: flex;
    align-items: center;
    margin: 0 auto;
    max-width: 320px;
    width: 100%;
  }
  &__content {
    position: relative;
    padding: 20px;
    width: 100%;
    background: $secondary-color;
    border-radius: 5px;
  }
  &__close {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 28px;
    height: 28px;
    background: $primary-color;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
  }
  &__logo {
    max-width: 190px;
    width: 100%;
    @include flex(space-between);
    margin: 0 auto;
    font-family: $font-family-bold;
    font-size: 36px;
    color: $primary-color;
    margin-bottom: 15px;
  }
  h3 {
    font-family: $font-family-semibold;
    font-size: 24px;
    color: $primary-color;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 5px;
  }
  h4 {
    font-family: $font-family-semibold;
    font-size: 16px;
    color: $black-color;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
  }
  form {
    width: 100%;
  }
  input[type="text"],
  input[type="tel"] {
    width: 100%;
    background-color: $contrasr-color;
    border-radius: 5px;
    border: 0;
    padding: 15px 15px 15px 60px;
    font-family: $font-family-regular;
    font-size: 14px;
    color: #1c1e27;
    &::-webkit-input-placeholder {
      color: #1c1e27;
    }
  }
  &__name {
    margin-bottom: 10px;
    background: url(../img/header/name.png) no-repeat 16px 11px;
  }
  &__tel {
    margin-bottom: 20px;
    background: url(../img/header/number.png) no-repeat 21px 8px;
  }
  &__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  form {
    display: flex;
    flex-direction: column;
    label {
      margin-bottom: 10px;
    }
    label,
    p {
      font-family: $font-family-regular;
      font-size: 12px;
      color: $black-color;
    }
    p {
      margin-bottom: 25px;
      b {
        font-family: $font-family-regular;
        font-size: 16px;
      }
    }
  }
  &__checkbox + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
  }
  &__checkbox + label::before {
    content: "";
    display: inline-block;
    width: 22px;
    height: 22px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 0;
    background-color: $contrasr-color;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    cursor: pointer;
  }
  &__checkbox:checked + label::before {
    border-color: $primary-color;
    background-color: $primary-color;
    background-image: url("../img/header/check.png");
    background-size: 12px;
  }
  button {
    padding: 15px 55px 15px 45px;
    font-family: $font-family-medium;
    font-size: 16px;
    color: $contrasr-color;
    background-color: $primary-color;
    text-transform: uppercase;
    margin: 0 auto;
    border-radius: 5px;
    background-image: url("../img/header/arrow.png");
    background-repeat: no-repeat;
    background-position-x: 155px;
    background-position-y: 15px;
  }
}

//                                  MAIN SCREEN

.main-screen {
  width: 100%;
  margin-top: 188px;
  padding: 60px 0 40px 0;
  background-color: $primary-color;
  background-image: url(../img/bg.png);
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: center;
  &__wrapper {
    max-width: 1130px;
    padding: 0 10px;
    margin: 0 auto;
  }
  &__title {
    font-family: $font-family-bold;
    font-size: 70px;
    color: $contrasr-color;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  &__text {
    font-family: $font-family-medium;
    font-size: 60px;
    color: $contrasr-color;
    text-transform: uppercase;
  }
  @media screen and (max-width: 1110px) {
    margin-top: 145px;
  }
  @media screen and (max-width: 768px) {
    background-size: cover;
    text-align: center;
    &__title {
      font-size: 48px;
    }
    &__text {
      font-size: 34px;
    }
  }
  @media screen and (max-width: 500px) {
    margin-top: 141px;
    &__title {
      font-size: 36px;
    }
    &__text {
      font-size: 24px;
    }
  }
  @media screen and (max-width: 420px) {
    margin-top: 133px;
  }
}

//                                  CONTAINER

.container {
  max-width: 1130px;
  width: 100%;
  padding: 10px;
  margin: 0 auto;
}

//                                  SERVICES

.services {
  width: 100%;
  text-align: left;
  padding: 50px 0 90px 0;
  &__title {
    position: relative;
    max-width: 500px;
    width: 100%;
  }
  h2 {
    font-family: $font-family-semibold;
    color: $black-color;
    font-size: 46px;
    text-transform: uppercase;
  }
  b {
    font-family: $font-family-medium;
    color: $primary-color;
  }
  p {
    position: absolute;
    top: 10px;
    left: 40px;
    width: 100%;
    font-family: $font-family-semibold;
    font-size: 74px;
    color: $secondary-color-opacity;
    text-transform: uppercase;
    z-index: -1;
  }
  @media screen and (max-width: 768px) {
    padding: 30px 0 40px 0;
    h2 {
      font-size: 34px;
    }
    p {
      font-size: 54px;
      width: 88%;
    }
  }
  @media screen and (max-width: 500px) {
    h2 {
      font-size: 28px;
    }
    p {
      font-size: 40px;
    }
  }
  @media screen and (max-width: 500px) {
    p {
      font-size: 34px;
    }
  }
}

//                                  CARD

.card {
  @include flex($flex-wrap: wrap);
  @media screen and(max-width:650px) {
    flex-direction: column;
  }
  &__item {
    padding-bottom: 50px;
    margin-bottom: 30px;
    padding-right: 100px;
    margin-right: 25px;
    width: max-content;
    @media screen and(min-width:1130px) {
      &:nth-child(4n),
      &:nth-child(4n + 1) {
        .card__text-wrapper {
          background: $primary-color;
        }
        .card__text-wrapper {
          color: $contrasr-color;
        }
        .card__title {
          color: $black-color;
        }
      }
    }
    @media screen and(max-width:1129px) {
      &:nth-child(2n) {
        .card__text-wrapper {
          background: $secondary-color;
        }
        .card__text-wrapper {
          color: $black-color;
        }
        .card__title {
          color: $primary-color;
        }
      }
    }
    // @media screen and(max-width:768px) {
    //   padding-right: 85px;
    // }
    @media screen and(max-width:650px) {
      margin-right: 0;
      width: 100%;
      padding: 0;
      margin-bottom: 40px;
    }
  }
  &__inner-wrapper {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 50%;
      height: 40%;
      top: 0;
      left: 20%;
      border: 2px solid $primary-color-light;
      border-radius: 20px;
      transform: rotateZ(-15deg);
      @media screen and(max-width:650px) {
        top: 50px;
        left: 120px;
      }
    }
    &::after {
      content: "";
      z-index: -2;
      position: absolute;
      width: 50%;
      height: 40%;
      bottom: -3%;
      left: 15%;
      border: 2px solid black;
      border-radius: 20px;
      transform: rotateZ(-15deg);
      // @media screen and(max-width:768px) {
      //   bottom: 0;
      // }
      @media screen and(max-width:650px) {
        height: 30%;
        bottom: 30px;
        left: 10px;
      }
    }
    @media screen and(max-width:650px) {
      width: 100%;
      padding-top: 65px;
    }
  }
  &__content-wrapper {
    position: relative;

    @include flex;
    margin-bottom: 50px;
    @media screen and(max-width:650px) {
      margin-bottom: 20px;
    }
  }
  &__more-link {
    font-family: $font-family-regular;
    text-align: right;
    font-size: $card-text-fz;
    margin-left: auto;
    max-width: 110px;
    cursor: pointer;
    @media screen and(max-width:500px) {
      font-size: $card-text-fz-mobile;
    }
  }
  &__text-wrapper {
    font-family: $font-family-regular;
    font-size: $card-text-fz;
    padding: 15px 110px 15px 15px;
    color: $black-color;
    position: relative;
    background: $secondary-color;
    width: 430px;
    height: 190px;
    border-radius: 15px;

    &::after {
      z-index: -1;
      content: "";
      border-radius: inherit;
      position: absolute;
      background: inherit;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      transform: skewY(-15deg) translateY(25%);
      // @media screen and(max-width:768px) {
      //   transform: skewY(-15deg) translateY(25%);
      // }
      @media screen and(max-width:650px) {
        display: none;
      }
    }
    // @media screen and(max-width:768px) {
    //   height: 160px;
    //   width: 350px;
    //   padding-right: 85px;
    //   font-size: $card-text-fz__mobile;
    // }
    @media screen and(max-width:1129px) {
      background: $primary-color;
      color: $contrasr-color;
    }
    @media screen and(max-width:650px) {
      text-align: center;
      padding: 75px 15px 15px;
      height: auto;
      width: 100%;
    }
    @media screen and(max-width:500px) {
      font-size: $card-text-fz-mobile;
    }
  }
  &__title {
    font-family: $font-family-semibold;
    margin-bottom: 30px;
    font-size: $card-title-fz;
    max-width: 430px;
    color: $primary-color;
    @media screen and(max-width:1129px) {
      color: $black-color;
    }
    @media screen and(max-width:650px) {
      max-width: 650px;
      text-align: center;
    }
    @media screen and(max-width:500px) {
      font-size: $card-title-fz-mobile;
    }
  }
  &__img-warpper {
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translate(-50%, -50%);
    width: 210px;
    height: 210px;

    background: grey;
    border-radius: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
    &::after {
      content: "";
      border-radius: inherit;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 105%;
      height: 105%;
      border: 2px solid black;

      border-left: 2px solid transparent;
      @media screen and(max-width:650px) {
        border: 1px solid black;
        border-left: 2px solid transparent;
        width: 105%;
        height: 105%;
      }
    }

    // @media screen and(max-width:768px) {
    //   width: 170px;
    //   height: 170px;
    // }
    @media screen and(max-width:650px) {
      left: 50%;
      top: 0;
      height: 130px;
      width: 130px;
    }
  }
}

//                                  QUESTION

.question {
  width: 100%;
  padding: 30px 0;
  &__title {
    font-family: $font-family-semibold;
    font-size: 28px;
    color: $black-color;
    text-transform: uppercase;
    padding: 10px 0 10px 10px;
    border-left: 0.5px solid $secondary-color;
    margin-bottom: 35px;
    b {
      color: $primary-color;
    }
    @media screen and(max-width:500px) {
      font-size: 24px;
    }
  }
  &__item {
    width: 100%;
    margin-bottom: 30px;
  }
  &__wrapper {
    width: 100%;
    overflow: hidden;
    max-height: 48px;
    &_active {
      max-height: 500px;
    }
  }
  &__header {
    @include flex(space-between);
    padding: 10px 8px 10px 27px;
    background: $secondary-color;
    border-radius: 45px;
    margin-bottom: 20px;
    @include transition;
    h4 {
      font-family: $font-family-medium;
      font-size: 16px;
      color: $black-color;
    }
    &_active {
      background: $primary-color-light;
      h4 {
        color: $contrasr-color;
      }
    }
  }
  &__plus {
    position: relative;
    width: 28px;
    height: 28px;
    background: $primary-color;
    border-radius: 45px;
    cursor: pointer;
    @include transition;
    > span {
      position: absolute;
      top: 13px;
      left: 6px;
      width: 16px;
      height: 2px;
      background: $contrasr-color;
      @include transition;
    }
    &::after {
      content: "";
      position: absolute;
      top: 6px;
      left: 13px;
      width: 2px;
      height: 16px;
      background: $contrasr-color;
      @include transition;
    }
    &_active {
      background: $contrasr-color;
      span,
      &::after {
        background: $primary-color;
      }
      span {
        opacity: 0;
      }
      &::after {
        transform: rotate(90deg);
      }
    }
  }
  &__text {
    position: relative;
    z-index: -1;
    transform: translateY(-300px);
    width: 100%;
    padding: 0 30px;
    font-family: $font-family-regular;
    font-size: 16px;
    color: $black-color;
    z-index: -1;
    @include transition(0.8s);
    &_active {
      transform: translateY(0);
    }
  }
  &__form {
    max-width: 830px;
    width: 100%;
    margin: 0 auto;
    h3 {
      font-family: $font-family-semibold;
      font-size: 22px;
      color: $primary-color;
      text-transform: uppercase;
      text-align: center;
      margin-bottom: 5px;
      b {
        color: $black-color;
      }
    }
    h4 {
      font-family: $font-family-medium;
      font-size: 16px;
      color: $black-color;
      text-align: center;
      margin-bottom: 20px;
    }
    form {
      width: 100%;
    }
    input[type="text"],
    input[type="tel"],
    textarea {
      width: 100%;
      background-color: $contrasr-color;
      border-radius: 5px;
      border: 1px solid #737373;
      padding: 15px 15px 15px 60px;
      font-family: $font-family-regular;
      font-size: 14px;
      color: #1c1e27;
      &::-webkit-input-placeholder {
        color: #1c1e27;
      }
    }
    textarea {
      flex-basis: 60%;
      padding-left: 15px;
      min-height: 110px;
      margin-bottom: 30px;
    }
  }
  &__group {
    flex-basis: 35%;
    margin-bottom: 30px;
  }
  &__name {
    margin-bottom: 10px;
    background: url(../img/header/name.png) no-repeat 16px 11px;
  }
  &__tel {
    background: url(../img/header/number.png) no-repeat 21px 8px;
  }
  &__checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  form {
    @include flex(space-between, $flex-wrap: wrap);
    label,
    p {
      font-family: $font-family-regular;
      font-size: 12px;
      color: $black-color;
    }
    p {
      b {
        font-family: $font-family-regular;
        font-size: 16px;
      }
    }
  }
  &__checkbox + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
  }
  &__checkbox + label::before {
    content: "";
    display: inline-block;
    width: 22px;
    height: 22px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid;
    background-color: $contrasr-color;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    cursor: pointer;
  }
  &__checkbox:checked + label::before {
    border-color: $primary-color;
    background-color: $primary-color;
    background-image: url("../img/header/check.png");
    background-size: 12px;
  }
  button {
    padding: 15px 75px;
    font-family: $font-family-medium;
    font-size: 16px;
    color: $contrasr-color;
    background-color: $primary-color;
    text-transform: uppercase;
    border-radius: 5px;
  }
  @media screen and (max-width: 768px) {
    form {
      flex-direction: column;
      label {
        margin-bottom: 10px;
      }
      p {
        margin-bottom: 25px;
      }
    }
    &__group {
      width: 100%;
    }
  }
}

//                                   CONTACT

.contact {
  width: 100%;
  position: relative;
  &__content {
    position: relative;
    max-width: 350px;
    width: 100%;
    padding: 55px 40px;
    background: $primary-color;
    &::after {
      content: "";
      position: absolute;
      // border: 100px solid transparent;
      // border-top: 200px solid $primary-color;
      // bottom: -100px;
      // right: -100px;
      z-index: -1;
      border: 60px solid transparent;
      border-top: 196px solid #093d88;
      bottom: -60px;
      right: -60px;
    }
    &::before {
      content: "";
      position: absolute;
      // top: -100px;
      // right: -100px;
      // border: 100px solid transparent;
      // border-bottom: 86px solid $primary-color;
      top: -60px;
      right: -60px;
      border: 60px solid transparent;
      border-bottom: 90px solid #093d88;
    }
  }
  &__header {
    position: relative;
    max-width: 200px;
    @include flex(space-between);
    margin-bottom: 15px;
    font-family: $font-family-regular;
    font-size: 18px;
    color: $contrasr-color;
  }
  &__telegram {
    position: absolute;
    top: 10px;
    right: -50px;
  }
  &__viber {
    position: absolute;
    top: 10px;
    right: -100px;
  }
  &__text {
    font-family: $font-family-regular;
    font-size: 16px;
    color: $contrasr-color;
  }
  &__map {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -2;
  }
  @media screen and (max-width:768px) {
    overflow: hidden;
    &__content{
      max-width: 225px;
      padding: 20px;
      &::after {
        border-top: 132px solid #093d88;
      }
      &::before {
        border-bottom: 55px solid #093d88;
      }
    }
    &__header{
      max-width: 150px;
      font-size: 14px;
    }
    &__logo{
      width: 40px;
    }
    &__telegram{
      right: -40px;
      width: 25px;
    }
    &__viber{
      right: -75px;
      width: 25px;
    }
    &__text{
      max-width: 200px;
      font-size: 14px;
    }
    &__map{
      height: 202px;
    }
  }
  @media screen and (max-width:500px){
    &__content{
      max-width: 225px;
      padding: 20px;
      &::after {
        border: 40px solid transparent;
        border-top: 132px solid #093d88;
        bottom: -40px;
        right: -40px;
      }
      &::before {
        top: -40px;
        right: -40px;
        border: 40px solid transparent;
        border-bottom: 55px solid #093d88;
      }
    }
  }
}

//                                   FOOTER

.footer {
  background: $secondary-color;
  padding: 30px 0;
  &__wrapper {
    display: flex;
    max-width: 1130px;
    padding: 0 10px;
    margin: 0 auto;
    @media screen and (max-width:768px) {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }
  &__header {
    max-width: 185px;
    width: 100%;
    margin-right: 50px;
    margin-bottom: 30px;
  }
  &__logo {
    @include flex($justify-content: unset);
    font-family: $font-family-bold;
    font-size: 24px;
    color: $primary-color;
    margin-bottom: 15px;
    img {
      width: 36px;
      margin-right: 5px;
    }
  }
  &__phone {
    @include flex(space-between);
    margin-bottom: 20px;
    img {
      margin-right: 10px;
    }
  }
  &__number {
    font-family: $font-family-medium;
    font-size: 14px;
    color: $black-color;
    b {
      font-family: $font-family-regular;
      font-size: 20px;
    }
  }
  &__text {
    font-family: $font-family-light;
    font-size: 10px;
    color: $black-color;
  }
  &__title {
    display: flex;
    width: 170px;
    padding-bottom: 10px;
    border-bottom: 1px dashed $black-color;
    font-family: $font-family-regular;
    font-size: 14px;
    color: $black-color;
    margin-bottom: 15px;
    svg {
      margin-right: 10px;
    }
  }
  &__links {
    max-width: 700px;
    @include flex(space-between, $flex-wrap: wrap);
    font-family: $font-family-regular;
    font-size: 12px;
    a {
      max-width: 220px;
      width: 100%;
      color: $black-color;
      margin-bottom: 10px;
    }
    @media screen and (max-width:768px) {
      max-width: 230px;
    }
  }
}
.back-to-top {
  position: fixed;
  right: 70px;
  bottom: 50px;
  padding: 5px 12px;
  background: $primary-color;
  font-family: $font-family-medium;
  font-size: 16px;
  color: $contrasr-color;
  cursor: pointer;
  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: -44px;
    border: 34px solid transparent;
    border-bottom: 10px solid $primary-color;
  }
  @media screen and (max-width:768px) {
    bottom: 30px;
    right: 30px;
  }
}
